.first-config-step-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
    min-height: 50%;
}

.first-config-step-container h1 {
    margin-bottom: 1rem;
}

.first-config-step-info {
    display: flex;
    flex-direction: column;
    /* margin-bottom: 3rem; */
    justify-content: center;
    align-items: flex-start;
    /* text-align: center; */
    font-size: 1.5rem;
}

.domain-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 3rem;
    gap: 2rem;
}


.domain-box input[type="checkbox"] {
    margin-right: 1rem;
    appearance: none;
    height: 30px;
    width: 30px;
    background-color: #e1e8ea;
    border: 1px solid #a6a6a6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 250ms linear;
    cursor: pointer;
}

.domain-box input[type="checkbox"]:checked {
    content: url(../img/svg/check-solid.svg);
    padding: 5px;
}


.domain-text {
    font-size: 2rem;
    height: 3rem;
}

.disabled-chckbox {
    content: url(../img/svg/check-solid.svg);
    padding: 5px;
    margin-right: 1rem;
    appearance: none;
    height: 30px;
    width: 30px;
    background-color: #42b757 !important;
    border: 1px solid #a6a6a6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 250ms linear;
    cursor: pointer;
}

.icon-placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
}

.disabled-check-icon {
    font-size: 20px;
}
