.third-config-step-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    /* text-align: center; */
    align-items: center;
    margin-top: 3rem;
    min-height: 50%;
    font-size: large;
    line-height: 2rem;
}

.third-config-step-container h2 {
    margin-bottom: 1rem;
}

.video-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 672px;
    margin: 2rem auto;
    box-shadow: 0 0 20px 5px #505050;
    transition: all 250ms linear;
}

.video-description {
    font-size: 1.2rem;
    margin-top: 1rem;
    max-width: 600px;
}


.more-btn {
    cursor: pointer;
    background: #f78d15 linear-gradient(210deg, #f3c22b, #f78d15);
    color: #fff;
    padding: 1em 3em;
    margin-top: 3rem;
    margin-bottom: 1rem;
    font-size: .8em;
    font-weight: 900;
    font-style: normal;
    text-transform: uppercase;
    display: inline-flex;
    /* Použijte inline-flex pro přizpůsobení velikosti obsahu */
    align-items: center;
    justify-content: center;
    border-width: 0;
    border-radius: 100vh;
    text-align: center;
    vertical-align: middle;
    outline: 0 !important;
    box-shadow: 0 0.6rem 1.133rem 0 rgba(0, 0, 0, 0.2),
        0 0.4rem 2.333rem 0 rgba(0, 0, 0, 0.15);
    transition: all 250ms linear;
    text-decoration: none;
}

.more-btn:hover {
    background: #fba23c;
    box-shadow: 0 0.625rem 1.25rem rgba(0, 0, 0, 0.1),
        0 1.25rem 3.75rem rgba(0, 0, 0, 0.1);
}
