.container {
    display: flex;
    min-height: 100dvh;
    justify-content: center;
    padding-left: 10rem;
    padding-top: 1rem;
    /* Posunutí od horního okraje */
}

.graph-table-container {
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
}

.bar-chart {
    position: relative;
    width: 150px;
    height: 150px;
    margin: 20px auto;
}

.graph-loading {
    width: 100%;
    height: 100%;
}

#graph-loading-line {
    stroke-dasharray: 145.37;
    stroke-dashoffset: 145.37;
    stroke-linecap: round;
    animation: draw 1s ease-out infinite;
}

@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}

