.table-container {
  width: 100%;
  min-height: 400px;
  display: flex;
  justify-content: center;
}

/* Styly pro Tabulator tabulku */
.date-box {
  margin-block: 3rem;
  display: flex;
  gap: 10rem;
}

#example-table {
  width: auto;
  margin-block: 3rem;
  font-size: 13px;

}

#example-table .tabulator {
  background-color: var(--background-color);
  border: 1px solid #ddd;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

#example-table .tabulator-header {
  background-color: #0AB0A4;
  color: white;
}

#example-table .tabulator-header .tabulator-col {
  padding: 5px;
  border-right: 1px solid #ddd;
  background-color: #0AB0A4;
  color: white;
}

#example-table .tabulator-header .tabulator-col:last-child {
  border-right: none;
}

.tabulator-col-title {
  color: white;
}

#example-table .tabulator-row {
  border-bottom: 1px solid #ddd;
}

#example-table .tabulator-row:nth-child(even) {
  background-color: #d3d3d380;
}

#example-table .tabulator-cell {
  padding: 10px;
  border-right: 1px solid #ddd;
}

#example-table .tabulator-cell:last-child {
  border-right: none;
}

#example-table .tabulator-row:hover {
  background-color: #0ab0a538;
}

#example-table .tabulator-calcs-holder {
  font-size: 12px;
}

/* Hlavní kontejner */
#main-container {
  position: relative;
  min-height: 100dvh;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.clickable-cell {
  cursor: pointer !important;
  color: #f5271d !important;
  font-weight: 800 !important;
  /* transition: all 100ms linear; */
}

.clickable-cell:hover {
  background-color: #0ab0a538;
}