.home-container {
    width: 60%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-align: justify;
    align-items: left;
    margin-top: 10%;
    scale: 1.2;
}

.home-container h1 {
    margin-bottom: 2rem;
    text-align: center;
}

.home-container .logo-container {
    margin-top: 5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #0D2534;
    padding: 40px;
}