.menu-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 70px;
    background-color: #0D2534;
    height: 100vh;
    min-height: 500px;
    position: fixed;
    top: 0;
    left: 0;
    transition: width 100ms linear;
    box-shadow: 0 0 20px 5px #505050;
    overflow: hidden;
}

.menu-container * {
    color: white;
}

.logo-box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    margin-bottom: 20px;
}

.small-logo-a {
    display: flex;
    justify-content: center;
}

.small-logo {
    width: 50px;
    height: auto;
}

.menu-items {
    width: 100%;
    padding-inline: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20rem;
    height: 45rem;
}

.menu-item {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    height: 40px;
    padding: 10px;
    outline: 1px solid transparent;
}

.menu-item:hover {
    outline: 1Px solid rgba(255, 255, 255, 0.5);
    transition: all 250ms linear;
}


.menu-text {
    opacity: 0;
    visibility: hidden;
    transition: opacity 2500ms linear, visibility 0ms linear 250ms, position 0ms linear; /* Delay visibility change */
    position: relative; /* Ensure the element retains its space */
    width: 120px;
    position: relative;
}

.menu-text.show {
    opacity: 1;
    visibility: visible;
    transition: opacity 500ms linear, visibility 0ms linear, position 0ms linear; /* No delay on showing */
    position: relative; /* Quicker transition for position change */
}

.menu-text.hide {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0ms linear, visibility 0ms linear, position 0ms linear; /* Instant visibility change */
    position: absolute;
}

.help {
    position: absolute;
    bottom: 30px;
}

.help-icon {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100vh;
    padding: 2px;
    cursor: pointer;
}

.help a {
    text-decoration: none;
    color: #00CECE;
}

.active {
    outline: 1Px solid white !important;
}
