.config-btn {
    cursor: pointer;
    background: #f78d15 linear-gradient(210deg, #f3c22b, #f78d15);
    color: #fff;
    padding: 1em 3em;
    margin-top: 2rem;
    font-size: 1em;
    font-weight: 900;
    font-style: normal;
    text-transform: uppercase;
    display: inline-flex;
    /* Použijte inline-flex pro přizpůsobení velikosti obsahu */
    align-items: center;
    justify-content: center;
    border-width: 0;
    border-radius: 100vh;
    text-align: center;
    vertical-align: middle;
    outline: 0 !important;
    box-shadow: 0 0.6rem 1.133rem 0 rgba(0, 0, 0, 0.2),
        0 0.4rem 2.333rem 0 rgba(0, 0, 0, 0.15);
    transition: all 250ms linear;
}

.config-btn:hover {
    background: #fba23c;
    box-shadow: 0 0.625rem 1.25rem rgba(0, 0, 0, 0.1),
        0 1.25rem 3.75rem rgba(0, 0, 0, 0.1);
}

.config-btn-loading {
    cursor: not-allowed;
    background: lightgray !important;
    color: #fff;
    padding: 1em 3em;
    margin-top: 2rem;
    font-size: 1em;
    font-weight: 900;
    font-style: normal;
    text-transform: uppercase;
    display: inline-flex;
    /* Použijte inline-flex pro přizpůsobení velikosti obsahu */
    align-items: center;
    justify-content: center;
    border-width: 0;
    border-radius: 100vh;
    text-align: center;
    vertical-align: middle;
    outline: 0 !important;
    box-shadow: 0 0.6rem 1.133rem 0 rgba(0, 0, 0, 0.2),
        0 0.4rem 2.333rem 0 rgba(0, 0, 0, 0.15);
    transition: all 250ms linear;
}

.config-btn-loading:hover {
    background: lightgray !important;
    box-shadow: 0 0.6rem 1.133rem 0 rgba(0, 0, 0, 0.2),
        0 0.4rem 2.333rem 0 rgba(0, 0, 0, 0.15);
}