.config-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
}

.heading-box {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.btn {
    cursor: pointer;
    background: #00AC9D;
    color: #fff;
    padding: 1em 3em;
    margin-top: 2rem;
    font-size: 1em;
    font-weight: 900;
    font-style: normal;
    max-width: 10rem;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    border-width: 0;
    border-radius: 100vh;
    justify-content: center;
    vertical-align: middle;
    outline: 0 !important;
    box-shadow: 0px 0px 20px gray;
    transition: all 250ms linear;
}

.btn:hover {
    background-color: #00cece;
    box-shadow: 0px 0px 10px gray;
}

.warning-wrapper {
    background-color: #fae3e5;
    display: flex;           /* Nastavení flexboxu */
    align-items: flex-start; /* Zarovnání prvků na začátek (nahoru) */
    padding: 1.5rem 1rem;
    border-radius: 10px;
    margin-bottom: 3rem;
    font-style: italic;
}

.warning-logo {
    font-size: 42.23px; /* Nebo jakákoli velikost, která vyhovuje */
    margin-right: 1rem;
    width: 42.23px;  /* Pevná šířka */
    height: 42.23px; /* Pevná výška */
    flex-shrink: 0; /* Zabrání zmenšování ikony */
    fill: #D81818;
    align-self: flex-start; /* Zarovnání ikony na začátek (nahoru) */
}

.warning-wrapper p {
    font-size: 20px;
    margin: 0;
    flex-grow: 1; /* Rozšíření textu tak, aby vyplnil zbývající místo */
}

.warning-wrapper p a {
    text-decoration: none;
    transition: all 200ms linear;
}

.warning-wrapper p a:hover {
    color: #00cece;
}

/* Původní styly pro ostatní wrappery */
.info-wrapper, .approved-wrapper {
    display: flex;
    align-items: center; /* Zarovnání prvků na střed vertikálně */
    padding: 1.5rem 1rem;
    border-radius: 10px;
    margin-bottom: 3rem;
    font-style: italic;
}

.info-wrapper {
    background-color: #fbf0d0;
}

.approved-wrapper {
    background-color: #bceedb;
}

.info-logo {
    font-size: 30px; /* Nebo jakákoli velikost, která vyhovuje */
    margin-right: 1rem;
    width: 30px;  /* Pevná šířka */
    height: 30px; /* Pevná výška */
    flex-shrink: 0; /* Zabrání zmenšování ikony */
}

.info-wrapper p, .approved-wrapper p {
    font-size: 20px;
    margin: 0;
}



