.step-bar {
    min-width: 100%;
    height: 3rem;
    background-color: #00AC9D;
    margin-top: 2rem;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
}

.step-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: inherit;
}

.step-item {
    color: #213A4B;
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 1.5em;
    transition: all 250ms linear;
    height: inherit;
}

.step-item:hover {
    background-color: #00cece;
    border-radius: 5px ;
}

.step-item p {
    color: #fff;
}

.slider {
    position: absolute;
    width: 33.3%;
    height: inherit;
    top: 0;
    left: 0;
    border: 3px solid #0A2C42;
    border-radius: 5px;
    transition: all 250ms ease-in-out;
    cursor: default;
}





