.setting-container {
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-align: justify;
    align-items: center;
    margin-top: 5%;
    /* scale: 1.2; */
    font-size: 120%;
}

.setting-container h1 {
    margin-bottom: 2rem;
}

.setting-container ul {
    list-style-type: decimal;
    line-height: 2rem;
}

.setting-info {
    margin-bottom: 1rem;
}

.ga4-img {
    width: 100%;
    margin-block: 3rem;
    box-shadow: 0 0 20px 5px #505050;
}