.date-inputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.date-display {
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.6rem 1.5rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #0AB0A4;
  color: white;
  transition: all 250ms linear;
  display: flex;
  justify-content: center;
  align-items: center;
  /* gap: 1.6rem; */
  
}

.date-display:hover {
  background-color: #00cece;
}

.calendar {
  fill: white;
  color: white;
  margin-bottom: 3px;
  font-size: 30px;
  margin-right: 1rem;
}

.triangle {
  fill: white;
  margin-left: 1.2rem;
  font-size: 1rem;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.date-picker-modal {
  background: white;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.fetch-btn-container {
  display: flex;
  justify-content: center;
  padding: 1rem;
  background: white;
  border-radius: 5px;
}

.fetch-btn {
  cursor: pointer;
  background: #f78d15 linear-gradient(210deg, #f3c22b, #f78d15);
  color: #fff;
  padding: 1em 3em;
  font-size: 1em;
  font-weight: 900;
  font-style: normal;
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-width: 0;
  border-radius: 100vh;
  text-align: center;
  vertical-align: middle;
  outline: 0 !important;
  /* box-shadow: 0 0.6rem 1.133rem 0 rgba(0, 0, 0, 0.2),
    0 0.4rem 2.333rem 0 rgba(0, 0, 0, 0.15); */
  transition: all 250ms linear;
}

.fetch-btn:hover {
  background: #fba23c;
  box-shadow: 0 0.625rem 1.25rem rgba(0, 0, 0, 0.1),
      0 1.25rem 3.75rem rgba(0, 0, 0, 0.1);
}

.rdrDateRangePickerWrapper {
  position: relative;
  z-index: 1000;
}

.rdrCalendarWrapper {
  background: white;
  border-radius: 5px;
}

.rdrDateDisplayWrapper {
  background-color: #0AB0A4;
  color: white;
}

.rdrDateDisplayWrapper input {
  cursor: default;
}

/* Změna barvy textu při hoveru a přidání rámečku */
.rdrDay:hover .rdrDayNumber span {
  color: #3498db;
  border-radius: 3px;
  padding: 2px;
}

/* Přidání padding a border-box pro správné zobrazení rámečku */
.rdrDayNumber span {
  padding: 2px;
  box-sizing: border-box;
}

/* Zajištění odstranění výchozího podtržení */
.rdrDayNumber span:after {
  content: none;
}

/* Změna barvy podtržení dneška */
.rdrDayToday .rdrDayNumber span {
  border-bottom: none;
}

/* Změna barvy výběru */
.rdrSelected {
  background: #0AB0A4 !important;
}

/* Změna barvy výběru okrajů */
.rdrStartEdge,
.rdrEndEdge,
.rdrInRange {
  background: #0AB0A4 !important;
}

/* Specifický styl pro dnešní den */
#root .rdrDayToday span::after {
  background: #f78d15;
  /* Oranžová barva pro dnešní den */
}

/* Specifický styl pro výběr rozsahu */
.rdrDayInPreview,
.rdrDayStartPreview,
.rdrDayEndPreview {
  border: none;
  background-color: #0ab0a55d;
}
