.second-config-step-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
    min-height: 50%;
}

.second-config-step-container h1 {
    margin-bottom: 1rem;
}

.second-config-step-info-box {
    display: flex;
    flex-direction: column;
    margin-bottom: 3rem;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    font-size: 1.5rem;
    width: 80%;
}

.second-config-step-container .advisio-link {
    text-decoration: none;
    color: #E7302A;
    font-weight: 600;
}

.second-config-domain-box {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    height: 3rem;
}

.second-config-domain-item {
    display: flex;
    justify-content: flex-start;
    gap: 1rem;
}

.second-config-domain-text {
    font-size: 2rem;
    height: 3rem;
}

.bar-chart {
    position: relative;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.graph-loading {
    width: 100%;
    height: 100%;
}

#graph-loading-line {
    stroke-dasharray: 145.37;
    stroke-dashoffset: 145.37;
    stroke-linecap: round;
    animation: draw 1s ease-out infinite;
}

@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}